* {
  box-sizing: border-box;
  margin: 0;
}
::selection {
  color: white;
  background-color: #333;
}
body {
  font-family: "Roboto", sans-serif;
  background-color: #eee;
  color: #333;
  display: grid;
  grid-template-columns: 220px 1fr;
  height: 100vh;
  gap: 34px;
  padding: 2rem 1rem;
}

/* Sidebar and Projects */
.sidebar,
.content {
  background-color: white;
  border-radius: 10px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  overflow-y: auto;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.08);
}
.project {
  display: flex;
  justify-content: space-between;
  margin: 5px 0;
  padding: 10px;
  background-color: white;
  border-radius: 10px;
  cursor: pointer;
}
.project:hover,
.project.active {
  background-color: #f5f5f5;
}
.add-project {
  display: flex;
  align-items: center;
  gap: 5px;
  width: 120px;
  height: 40px;
  margin: 0 auto;
  padding: 7px;
  font-size: 14px;
  color: #333;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.2s;
}
.add-project:hover {
  background-color: #333;
  color: white;
  border: 1px solid #333;
}
.add-project .icon {
  font-size: 35px;
}
.project-form {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: -5px;
}
.project-form input[name="add-project-name"] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 10px;
  outline: none;
  transition: 0.2s;
}
.project-form input[name="add-project-name"]:focus {
  border: 1px solid #333;
}
.project-form button[type="reset"],
.project-form button[type="submit"] {
  padding: 7px 5px;
  width: calc(50% - 5px);
  color: #333;
  background-color: white;
  border: 1px solid #333;
  border-radius: 10px;
  cursor: pointer;
}
.project-form button[type="submit"] {
  background-color: #333;
  color: white;
}

/* Content and Tasks */
.content {
  padding: 20px 35px;
}
h1.project-name {
  margin: 15px 0;
}
.task-form {
  display: flex;
  align-items: center;
  gap: 10px;
  height: 50px;
}
input[name="task-name"],
input[name="due-date"],
select[name="priority"],
.add-task {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 10px;
  font-size: 16px;
  background-color: white;
  color: #333;
  transition: 0.2s;
  flex: 1;
}
input[name="task-name"]:focus,
input[name="due-date"]:focus,
select[name="priority"]:focus {
  outline: none;
  border: 1px solid #333;
}
input[name="task-name"] {
  flex: 1.7;
}
.add-task {
  padding-right: 15px;
  cursor: pointer;
  transition: 0.2s;
}
.add-task:hover {
  background-color: #333;
  color: white;
  border: 1px solid #333;
}
.add-task .icon {
  font-size: 35px;
  margin-right: 5px;
}
.task-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 20px;
}
.task {
  display: flex;
  justify-content: space-between;
  border: 1px solid #ccc;
  border-radius: 10px;
  height: 50px;
  padding: 0 20px;
  background-color: #f5f5f5;
}
.task > div {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
p.due-date {
  font-style: italic;
  font-size: 14px;
}
p.priority {
  font-size: 12px;
  color: white;
  padding: 3px 5px;
  border-radius: 6px;
  background-color: #333;
}
input[name="check-task"] {
  width: 18px;
  height: 18px;
  accent-color: #333;
  border-radius: 6px;
  margin-right: -5px;
  cursor: pointer;
}
input[name="check-task"]:checked ~ .task-title {
  text-decoration: line-through;
}
.edit-task {
  cursor: pointer;
}

/* Project Popup & Task Popup */
.project-popup-layer,
.task-popup-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.25);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.project-popup,
.task-popup {
  position: relative;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  max-width: 330px;
  margin: 0 10px;
}
.project-popup h2,
.task-popup h2 {
  margin: 10px 0 30px;
}
.cancel-edit-project, 
.cancel-edit-task {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 30px;
  height: 30px;
  font-size: 22px;
  font-weight: bold;
  border: 1px solid #333;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.2s;
}
.cancel-edit-project:hover,
.cancel-edit-task:hover {
  background-color: #333;
  color: white;
  border-radius: 50%;
}
.project-popup-form,
.task-popup-form {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 20px;
  margin: 10px 0;
}
input[name="popup-project-name"],
input[name="popup-task-name"],
select[name="popup-priority"],
input[name="popup-due-date"] {
  width: 100%;
  height: 40px;
  padding: 10px;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 10px;
  outline: none;
  transition: 0.2s;
}
input[name="popup-project-name"]:focus,
input[name="popup-task-name"]:focus,
select[name="popup-priority"]:focus,
input[name="popup-due-date"]:focus {
  border: 1px solid #333;
}
.delete-project,
.rename-project,
.delete-task,
.update-task {
  padding: 10px 5px;
  border: 1px solid #333;
  border-radius: 10px;
  background-color: white;
  font-size: 14px;
  width: calc(50% - 10px);
  cursor: pointer;
}
.delete-project,
.delete-task {
  border: 1px solid #f40914;
  color: #f40914;
}
.rename-project,
.update-task {
  background-color: #333;
  color: white;
}

